


























export default {
	name: 'age-form',
	props: {
		title: { //标题
		    type: String,
		    default: ''
		},
	    show: { //是否显示模态框
	        type: [Boolean, String],
	        default: false
	    },
		fieldTag:{
			type: Object
		},
		fieldSuperTag:{
			type: Object
		}
	},
	data() {
		return {
			fromValue:'',
			toValue:'',
			isOpen: false,
			showAnimation:false,
			inWatchTimeout:false,
			inMethodsTimeout:false
		};
	},
	watch: {
	    show(val) {
	        if(this.inWatchTimeout){
	            return
	        }
	        if (val) {
	        	this.isOpen = val
	            setTimeout(() => {
	            	this.showAnimation = val
	            }, 50)
	        } else {
	            this.showAnimation = val
	            this.inWatchTimeout = true
	        	setTimeout(() => {
	        		this.isOpen = val
	                this.inWatchTimeout = false
	        	}, 200)
	        }
	    }
	},
	created() {
		this.isOpen = this.show
		setTimeout(() => {
			this.showAnimation = this.show
		}, 50)
	},
	methods:{
		closeModal() {
		    this.showAnimation = false
		    this.inMethodsTimeout = true
		    setTimeout(() => {
		    	this.isOpen = false
		        this.inMethodsTimeout = false
		        this.$emit('close')
		    }, 200)
		},
		submit:function(){
			this.closeModal();
			this.$emit('confirm', {
				fromValue: this.fromValue,
				toValue: this.toValue,
				tag:this.fieldTag,
				superTag:this.fieldSuperTag
			});
		}
	},
	onLoad() {
	    
	}
}
